import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import browseCourse from "../json/browseCourse.json";
import courseList from "../json/courses.json";

const CourseByCity = (props) => {
  const [courses, setCourses] = useState([]);
  const [detail, setDetail] = useState(null);
  const [cityPath, setCityPath] = useState("");
  const { city, state } = props.match.params;

  useEffect(() => {
    const url = "/state/";
    console.log(city, state);
    axios
      .post(url + state, { city })
      .then((data) => {
        let new_data = {};
        data.data.map((per, i) => {
          if (i === 0 || per.clubName !== data.data[i - 1].clubName) {
            new_data[per.clubName] = { sub: [] };
          }
          if (new_data[per.clubName]) {
            new_data[per.clubName].sub.push(per);
          }
        });
        console.log(new_data);
        let arr = [];
        for (const key in new_data) {
          let per = new_data[key];
          arr.push({ ...per.sub[0], ...{ sub: per.sub } });
        }
        arr[0].expand = true;
        console.log(arr);
        setCourses(arr);
        setDetail(arr[0].sub[0]);
      })
      .catch((error) => {
        console.error(error);
      });
  }, []);

  const toggleArrow = (index) => {
    const newCourses = [...courses];
    newCourses[index].expand = !courses[index].expand;
    setCourses(newCourses);
  };

  const courseDetail = (per) => {
    console.log(per, "INSIDE COURSE DETAIL");
    setDetail(per);
  };

  const renderCourses = (el, index) => {
    const arrowClass = "arrow " + (el.expand ? "down" : "right");
    return (
      <div className="list-wrapper" key={el.id}>
        <div
          className="courses--wrapper--lists--club"
          onClick={() => {
            toggleArrow(index);
          }}
        >
          <span style={{ paddingRight: 5 }}>{el.clubName}</span>
          <span className={arrowClass}></span>
        </div>
        <div
          className={
            el.expand
              ? "courses--wrapper--lists--sub"
              : "courses--wrapper--lists--sub-shrink"
          }
        >
          {el.sub.map((per) => {
            return (
              <div
                key={per.id}
                onClick={() => {
                  courseDetail(per);
                }}
                className="courses--wrapper--lists--sub-title"
                style={{
                  paddingLeft: "12px",
                  marginBottom: "4px",
                  color: per.id === detail?.id ? "rgb(120, 188, 65)" : null,
                }}
              >
                {per.coursename}
              </div>
            );
          })}
        </div>
      </div>
    );
  };

  const renderCityName = (el) => {
    if (state === el.state) {
      return el.name;
    }
  };

  return (
    <>
      <div className="courses" style={{ paddingTop: "36px" }}>
        <div className="courses--header">
          <div>
            <a href="#/courses" className="courses--header--directory">
              Course Directory
            </a>
            <span className="arrow-right"></span>
          </div>
          <div>
            <a
              href={"#/courses/state/" + state}
              className="courses--header--city"
            >
              {browseCourse.map(renderCityName)}
            </a>
            <span className="arrow-right"></span>
          </div>
          <div>
            <a
              href={"#/courses/state/" + state + "/" + city}
              className="courses--header--city"
            >
              {city}
            </a>
          </div>
        </div>
        <div className="courses--wrapper">
          <div className="courses--wrapper--lists">
            {courses.map(renderCourses)}
          </div>
          {detail && (
            <>
              <div className="courses--wrapper--details">
                <div className="courses--container">
                  <div className="courses--wrapper--details--left">
                    <div className="courses--wrapper--details--left--title">
                      {detail.coursename}
                    </div>
                    <div className="courses--wrapper--details--left--desc">
                      {detail.description}
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--main">
                        Street :
                      </div>
                      <div className="courses--wrapper--details--left--sub">
                        {detail.street}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--main">
                        Phone :
                      </div>
                      <div className="courses--wrapper--details--left--sub">
                        {detail.phone}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--main">
                        Fax :
                      </div>
                      <div className="courses--wrapper--details--left--sub">
                        {detail.fax}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--main">
                        Website :
                      </div>
                      <div className="courses--wrapper--details--left--sub">
                        <a href={detail.courseUrl} target="_blank">
                          {detail.courseUrl}
                        </a>
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--main">
                        Date :
                      </div>
                      <div className="courses--wrapper--details--left--date">
                        {detail.noDate.date.substr(0, 10)}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--map">
                      <div className="courses--wrapper--details--left--main">
                        Google Map :
                      </div>
                      <div className="courses--wrapper--details--left--sub">
                        <a
                          href={detail.mapUrl ? detail.mapUrl : null}
                          target="_blank"
                          className="courses--wrapper--details--left--googleURL"
                        >
                          Map
                          <img
                            src="https://nrange.com/webappv3/assets/nrange-logo-footer.png"
                            alt="nrange-logo-footer"
                          />
                        </a>
                      </div>
                    </div>
                  </div>
                  <div className="courses--wrapper--details--right">
                    <div className="courses--wrapper--details--right--title">
                      Golf courses staff
                    </div>
                    <div className="courses--wrapper--details--right--designer">
                      {detail.designer}
                    </div>
                    <div className="courses--wrapper--details--right--onsite">
                      {detail.onsitegolfpro}
                    </div>
                    <div className="courses--wrapper--details--right--manager">
                      {detail.generalManager}
                    </div>
                    <div className="courses--wrapper--details--right--super">
                      {detail.superintendent}
                    </div>
                  </div>
                </div>
                <div
                  className="courses--container"
                  style={{ paddingTop: "24px", paddingBottom: "104px" }}
                >
                  <div className="courses--wrapper--details--left">
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        Type
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.type}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        Year Built
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.yearBuilt}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        Dress Code
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.dresscode}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        Spikes
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.spikes}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        restaurant
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.restaurant}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        access
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.access}
                      </div>
                    </div>
                  </div>
                  <div className="courses--wrapper--details--right">
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        guests
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.guests}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        Rental Clubs
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.rentalclubs}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        carts
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.carts}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        cartfees
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.cartfees}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        pullcarts
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.pullcarts}
                      </div>
                    </div>
                    <div className="courses--wrapper--details--left--row">
                      <div className="courses--wrapper--details--left--var">
                        caddies
                      </div>
                      <div className="courses--wrapper--details--left--index">
                        {detail.caddies}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </div>
    </>
  );
};

export default CourseByCity;
